import { DOCUMENT } from '@angular/common';
import { ElementRef, HostListener, Inject, Renderer2, ViewChild } from '@angular/core';
import { Component, OnInit } from '@angular/core';
import { Character } from '@app/interface/character.interface';
import { CharacterService } from '@app/shared/services/character.service';
import { fromEvent } from 'rxjs';
import { pluck } from 'rxjs/operators';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
   
  @ViewChild('btn') btn:ElementRef

  style:any = {}
  characters:Character[]
  
  constructor(
    private characterService:CharacterService,
     @Inject(DOCUMENT) private document:Document,
     private render:Renderer2
    ) { }

  ngOnInit(): void {
    this.scroll()
    this.style = {
       fontSize:'16px'
    }

    this.getCharacter()
  }

  getCharacter(){
    this.characterService.getCharacters()
         .pipe(pluck('results')).subscribe(
          ( res:any ) => this.characters = res
    )
  }

  trackByItem(index,item){
    return item.id
  }

  scroll(){
    fromEvent(document,'scroll').subscribe(
      ()=>{
          if(this.document.documentElement.scrollTop > 200) {
            this.render.removeClass(this.btn.nativeElement,'hide')
            this.render.addClass(this.btn.nativeElement,'show')
        }else{
            this.render.removeClass(this.btn.nativeElement,'show')
            this.render.addClass(this.btn.nativeElement,'hide')
        }
      }
    )
     
  }

  subir(){
      this.document.documentElement.scrollIntoView({ block: 'start' , behavior:'smooth' })
  }


}
